/* MONTSERRAT FONT */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,700&display=swap');


/* CSS RESET */
*, *::after, *::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App{
    font-family: "Montserrat", sans-serif;
}

body{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

article {
    display: flex;
    width: 85%;
    margin: 0 auto;
}

article .producto_img img{
    width: auto;
    margin: 0 1em;
    height: 20em;
}

article header h1{
    font-size: 35px;
}

article .descripcion{
    margin: 1em 0;
}

article .precio{
    font-weight: 700;
    font-size: 25px;
}

article .sku{
    margin: 1em 0;
    font-style: italic;
}

article button[type=submit]{
    margin-top: 2em;
    padding: 1em 2em;
    border: none;
    font-family: "Montserrat", sans-serif;
    background-color: rgb(121, 121, 248);
    color: white;
    font-weight: 700;
}
